<template>
  <div class="row">
    <div class="title">
      <h3>生命建造程序</h3>
      <h1>{{ datas.SBName }}</h1>
    </div>
    <div class="formInfo">
      <p>本表個人資料將作建檔處理，並於日後會務活動運作之目的內，由教會及同工為聯絡通訊、關懷及相關合理運用。我們會盡善良管理人責任，妥善保管資料，避免外洩或不當用途之使用。</p>
      <ul class="text-info">
        <li>報名條件：{{ datas.SUC }}</li>
        <li>上課時間：{{ datas.SBDate }}</li>
        <li>地點：{{ datas.SBL }}</li>
        <li>報名日期：{{ datas.SUDate }}</li>
      </ul>
    </div>
    <hr>
    <div class="formBox">
      <form class="form-horizontal">
        <div class="form-group" style="margin-bottom:0;">
          <div class="col-sm-offset-2 col-sm-10">
            <label class="text-danger">* 必填</label>
          </div>
        </div>
        <div class="form-group">
          <div class="col-sm-offset-2 col-sm-10">
            <div class="checkbox">
              <label>
                <input type="checkbox" v-model="isNotInGroup"> 我目前尚未加入小組
              </label>
            </div>
          </div>
        </div>
        <div class="form-group row">
          <label for="groupType" class="col-sm-2 control-label">
            <span class="text-danger">* </span>組別
          </label>
          <div class="col-sm-10">
            <select name="groupType" id="groupType" class="form-control" :disabled="isNotInGroup" v-model="groupTypeName">
              <option v-for="groupType in groupClass" :key="groupType.group">{{ groupType.group }}</option>
            </select>
          </div>
        </div>
        <div class="form-group row">
          <label for="group" class="col-sm-2 control-label">
            <span class="text-danger">* </span>小組
          </label>
          <div class="col-sm-10">
            <select name="group" id="group" class="form-control" :disabled="isNotInGroup" v-model="groupName">
              <option v-for="group in groups" :value="group" :key="group.split('.')[0]">{{ group }}</option>
            </select>
          </div>
        </div>
        <div class="form-group row">
          <label for="name" class="col-sm-2 control-label">
            <span class="text-danger">* </span>姓名
          </label>
          <div class="col-sm-10">
            <input type="text" id="name" name="name" class="form-control" placeholder="請填寫姓名" v-validate="'required'" v-model="name">
            <span class="text-danger" v-show="errors.has('name')">{{ errors.first('name') }}</span>
          </div>
        </div>
        <div class="form-group row">
          <label for="phone" class="col-sm-2 control-label">
            <span class="text-danger">* </span>手機
          </label>
          <div class="col-sm-10">
            <input type="text" id="phone" name="phone" class="form-control" placeholder="請填寫手機" v-validate="'required|twPhone'" v-model="phone">
            <span class="text-danger" v-show="errors.has('phone')">{{ errors.first('phone') }}</span>
          </div>
        </div>
        <div class="form-group row">
          <label for="email" class="col-sm-2 control-label">Gmail</label>
          <div class="col-sm-10">
            <input type="email" class="form-control" id="email" name="email" placeholder="請填寫 google 信箱" v-validate="'email|isGmail'" v-model="gmail">
            <span class="text-danger" v-show="errors.has('email')">{{ errors.first('email') }}</span>
          </div>
        </div>
        <div class="form-group row">
          <label for="church" class="col-sm-2 control-label">所屬教會</label>
          <div class="col-sm-10">
            <select name="church" id="church" class="form-control" v-model="church">
              <option v-for="church in datas.Church" :key="church">{{ church }}</option>
            </select>
          </div>
        </div>
        <div class="form-group row">
          <div class="col-sm-offset-2 col-sm-10">
            <button type="submit" class="btn btn-default" @click.prevent="submit">
              <span v-if="!isSending">報名</span>
              <span v-else>
                <i class='fa fa-circle-o-notch fa-spin'></i> Sending...
              </span>
            </button>
          </div>
        </div>
      </form>
    </div>
  </div>
</template>
<script>
import axios from 'axios';
import swal from 'sweetalert';
import { getParameterByName } from '../utils';

export default {
  data() {
    return {
      isNotInGroup: false,
      groupTypeName: '',
      groupName: '',
      name: '',
      gmail: '',
      church: '',
      makeup: '',
      phone: '',
      groupClass: [],
      isSending: false
    }
  },
  props: ['datas'],
  computed: {
    groups() {
      let v = this.groupTypeName, g = this.groupClass;
      for (var i = 0, l = g.length; i < l; i++) {
        if (g[i].group == v) {
          return g[i].list;
        }
      }
      return [];
    }
  },
  watch: {
    isNotInGroup(value) {
      if (value) {
        this.groupTypeName = '';
        this.groupName = '';
      }
    }
  },
  methods: {
    callAddSubSign(chkStatus, cateGory, mid) {
      let self = this;
      axios.post('/api/AddSubSign.aspx', {
        "S_ID": self.datas.S_ID,
        "Category": cateGory,
        "gcroup": self.groupTypeName,
        "group": self.groupName,
        "name": self.name,
        "G_mail": self.gmail,
        "Church": self.church,
        "Phone": self.phone,
        "ChkStatus": chkStatus,
        "MID": mid,
      }, {
        headers: { 'content-type': 'application/json' }
      }).then(function(response) {
          self.isSending = false;

          swal({
            title: "報名成功",
            icon: "success"
          }).then((value) => {
            self.$emit('linkToSuccessPage', response);
          });

      }).catch(function(error) {
        self.isSending = false;
        swal("Oops...", error, "error");
      });
    },
    submit() {
      if (!this.isSending) {
        let self = this;
        //判斷小組有沒有填
        if (!self.isNotInGroup) {
          if (!self.groupTypeName || !self.groupName) {
            swal("Oops...", '請選擇組別及小組', "error");
            return;
          }
        }

        this.$validator.validateAll().then(() => {
          self.isSending = true;

          axios.post('/api/ChkMember.aspx', {
            GroupName: self.groupName,
            Ename: self.name,
            Phone: self.phone,
          }).then((response) => {
          // axios.get('http://localhost:3000/ChkMember').then((response) => {
            const chkStatus = response.data.ChkStatus;
            const ChkMsg = response.data.ChkMsg;
            const mid = response.data.MID;
            const cateGory = getParameterByName('id');

            if (ChkMsg === '') {
              this.callAddSubSign(chkStatus, cateGory, mid);
            } else {
              console.log('response', response);
              swal(ChkMsg, {
                buttons: {
                  yes: "是",
                  no: "否",
                  cancelCheck: "取消",
                  default: false,
                },
                closeOnClickOutside: false
              })
              .then((value) => {
                switch (value) {
                  case "yes":
                    this.callAddSubSign(chkStatus, cateGory, mid);
                    break;
                  case "no":
                    this.callAddSubSign(0, cateGory, mid);
                    break;
                  case "cancelCheck":
                    self.isSending = false;
                    break;
                  default:
                    return false;
                }
              });
            }
            
          }).catch((error) => {
            self.isSending = false;
            swal("Oops...", error, "error");
          });


        }).catch((error) => {
          swal("Oops...", "請確實填寫需要的資訊", "error");
        });

      }
    },
  },
  created() {
    var self = this;

    // axios.get('http://localhost:3000/GetGroupItem')
    axios.get('/api/GetGroupItem.aspx')
      .then(function(response) {
        self.groupClass = response.data.DataInfo;
      })
      .catch(function(error) {
        console.log(error);
      });
  }
}
</script>
<style scoped>
.formInfo {
  display: inline-block;
  max-width: 600px;
  text-align: left;
  font-size: 12px;
}

ul {
  padding-left: 1.5em;
}

.form-horizontal {
  text-align: left;
  margin-bottom: 40px;
}

.formBox {
  max-width: 600px;
  margin: 0 auto;
}

.text-danger {
  font-size: 12px;
}
</style>





