<template>
  <div id="app">
    <img src="./assets/title.jpg" id="mainBg">
    <div class="container">
      <transition-group name="fade" mode="out-in">
        <ErrorPage
          v-if="showError"
          :msg="errorMsg"
          key="errorPage"
        />
        <SignupForm
          v-if="!showSuccess && !showError" 
          @linkToSuccessPage="linkToSuccessPage($event)"
          :datas="datas"
          @showError="showError($event)"
          key="signupForm"
        />
        <SuccessPage
          :data="successData"
          key="successPage"
          v-if="showSuccess && !showError"
        />
      </transition-group> 
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import SignupForm from './components/SignupForm'
import SuccessPage from './components/SuccessPage';
import ErrorPage from './components/ErrorPage';
import css from './css/style.css'
import test from './css/test.sass'
// import sweetalert from './css/sweetalert.css'
import { getParameterByName } from './utils';

export default {
  name: 'app',
  data() {
    return {
      showSuccess: false,
      successData: {},
      datas: {
        "SBName": "",
        "SUC": "",
        "SBDate": "",
        "SBL": "",
        "SUDate": "",
        "Church": [],
        "MakeUp": []
      },
      showError: true,
      errorMsg: '',
      categoryId: '',
    };
  },
  methods: {
    linkToSuccessPage(e) {
      this.showSuccess = true;
      this.successData = e.data;
    },
    showError(e) {
      this.showError = true;
      this.errorMsg = e;
    },
  },
  components: {
    SignupForm,
    SuccessPage,
    ErrorPage,
  },
  created () {
    const Category_ID = getParameterByName('id');
    let self = this;

    if(!Category_ID) {
      this.showError = true;
      this.errorMsg = '發生錯誤!';
    } else {
      // axios.get('http://localhost:3000/GetSUBInfo')
      axios.post('/api/GetSUBInfo.aspx',{ Category_ID })
      .then(function(response) {
        if (response.data.ErrMsg) {
          self.errorMsg = response.data.ErrMsg;
        } else {
          self.datas = response.data;
          self.showError = false;
        }
      })
      .catch(function(error) {
        self.errorMsg = '發生錯誤!';
        console.warn(error);
      });
    }
  }
}
</script>
<style>
.fade-enter{opacity: 0;}
.fade-enter-active{transition: opacity 1s;}
.fade-leave{}
.fade-leave-active{
  transition: opacity .5s;
  opacity: 0;
}
</style>







