// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import VeeValidate from 'vee-validate';

Vue.config.productionTip = false

const config = {
  locale: 'tw', 
  dictionary: {
    tw: {
      messages:{
        email: () => '請填寫正確 Email 格式',
        required: () => '此欄位必填',
        isGmail: () => '請填寫正確 Gmail 格式',
        twPhone: () => '請填寫正確手機格式'
      }
    }
  }, 
};
VeeValidate.Validator.extend('isGmail', {
  getMessage: (field) => 'The ' +field + ' value is not truthy.',
  validate: mail => {
    var testExpression = /^[a-z0-9](\.?[a-z0-9]){5,}@g(oogle)?mail\.com$/; 
    return testExpression.test( mail ); 
  }
});
VeeValidate.Validator.extend('twPhone', {
  getMessage: (field) => 'The ' +field + ' value is not truthy.',
  validate: phoneNumber => {
    phoneNumber = phoneNumber.toString();
    var cellphone = /^[09]{2}[0-9]{8}$/; 
    if ( cellphone.test( phoneNumber ) ) {
      return true; 
    }else{return false;} 
  }
});
Vue.use(VeeValidate, config);

/* eslint-disable no-new */
new Vue({
  el: '#app',
  template: '<App/>',
  components: { App }
})



